:root {
  --w-blue-color:#8729FF;
  --w-blue-color-h:#6C1ECE;
  --w-dark-blue-color:#1C1130;
  --w-pink:#ff00ff;
  --w-bg:#151722;
  --w-text-color:#72778F;
  --w-title-color:#ffffff;
  --w-border-color:#72778F;
  --w-border-color-h:var(--w-blue-color);
  --w-container-p: 0 20px;
  --w-bg-box:#22232F;
  --w-ff:24px;
  --w-fl:34px;
  --w-ffh1:34px;
  --w-flh1:44px;
  --w-th:0.2s;
  --w-br:12px;
  --w-mbh1:20px;
  --w-video-box: calc(50% - 16px);
  --w-play-bg:var(--w-blue-color);
  --w-play-i:var(--w-title-color);
  --w-play-bg-h:var(--w-blue-color-h);
  --w-play-i-h:var(--w-title-color);
  --w-bg-button:var(--w-blue-color);
  --w-color-button:#ffffff;
  --w-bg-button-h:var(--w-blue-color-h);
  --w-color-button-h:#ffffff;
  --w-br-button:12px;
}

@media (min-width: 768px) {
  :root {
    --w-ff:28px;
    --w-fl:38px;
    --w-ffh1:44px;
    --w-flh1:64px;
    --w-mbh1:30px;
    --w-container-p:0 30px;
    --w-video-box: calc(25% - 16px);
  }
}
@media (min-width: 1200px) {
  :root {
    --w-ff:32px;
    --w-fl:48px;
    --w-ffh1:64px;
    --w-flh1:80px;
    --w-mbh1:50px;
    --w-container-p:0 50px;
  }
}
html,
body {
  margin: 0;
  height: 100%;
  padding: 0;
  display: block;
  min-height: 100%;
  color: var(--w-text-color);
  background-color: var(--w-bg);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 20px;
}

a {
  color: var(--w-pink);
}
a:hover {
  text-decoration: none;
}

.pink {
  color: var(--w-pink);
  font-weight: normal;
}

h1 {
  font-size: var(--w-ffh1);
  line-height: var(--w-flh1);
  text-align: center;
  nav-down: var(--w-mbh1);
  color: var(--w-title-color);
  margin: 0;
}

h2,
h3,
h4,
h5,
h6 {
  font-size: var(--w-ff);
  line-height: var(--w-fl);
  text-align: center;
  nav-down: var(--w-mbh1);
  color: var(--w-title-color);
}

h3 {
  text-align: left;
  margin: 0;
}

.small-title {
  color: var(--w-title-color);
}

#header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  margin: 0 0 50px;
}
#header .logo-wfs {
  max-width: 200px;
  display: flex;
}
#header .logo-wfs img {
  display: block;
  width: 100%;
  height: auto;
}
#header .menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
  margin: 0 0 0 20px;
}
#header .menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
#header .menu ul a {
  color: var(--w-title-color);
  transition: color var(--w-th) ease-in-out;
}
#header .menu ul a.active, #header .menu ul a:hover {
  color: var(--w-pink);
}
#header .menu li {
  margin: 0 0 0 10px;
}

.home-menu #wfsv-wrapper {
  padding: 120px 0 0;
}
.home-menu #header {
  display: flex;
  position: fixed;
  width: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  background: var(--w-bg-box);
  border-bottom: 1px solid var(--w-border-color);
}
.home-menu #header .wfsv-container {
  display: flex;
  margin: 0 auto;
}

@media (max-width: 1199px) {
  .home-menu #header {
    top: 0;
    margin: 0;
    padding: 10px 0;
  }
  .home-menu #header .logo-wfs {
    max-width: 150px;
  }
  .home-menu #header .wfsv-container {
    justify-content: space-between;
  }
  .home-menu #header .menu {
    position: fixed;
    top: 0;
    left: 100%;
    width: 100%;
    height: 100%;
    background: var(--w-bg-box);
    transition: left var(--w-th) ease-in-out;
    margin: 0;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 60px 0 0;
    z-index: 10;
  }
  .home-menu #header .menu .open-sidebar-list {
    position: absolute;
    top: 10px;
    right: 10px;
  }
  .home-menu #header .menu ul {
    justify-content: flex-start;
    flex-flow: column;
    width: 100%;
  }
  .home-menu #header .menu ul li {
    margin: 0 0 15px;
    width: 100%;
  }
  .home-menu #header .menu ul a {
    border-radius: 0;
    padding: 10px 20px;
    display: block;
    box-sizing: border-box;
  }
  .home-menu.open-mobile-menu #header .menu {
    left: 0;
  }
}
.wfsv-container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto 50px;
  padding: var(--w-container-p);
  box-sizing: border-box;
}

.wfs-holder {
  margin: 0 0 50px;
}

.wfsv-list {
  padding: 0;
  margin: 0;
}
.wfsv-list li {
  margin-bottom: 10px;
}

#footer {
  padding: 20px 0;
}
#footer p {
  text-align: center;
  margin: 0;
}

#header .menu ul .wfsv-button,
.wfsv-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  height: 40px;
  background: var(--w-bg-button);
  color: var(--w-color-button);
  border-radius: var(--w-br-button);
  transition: all var(--w-th) ease-in-out;
  text-decoration: none;
}
#header .menu ul .wfsv-button.wfsv-button-full,
.wfsv-button.wfsv-button-full {
  width: 100%;
  box-sizing: border-box;
}
#header .menu ul .wfsv-button:hover,
.wfsv-button:hover {
  background: var(--w-bg-button-h);
  color: var(--w-color-button-h);
}

.hidden-element {
  position: absolute;
  left: -9999px;
  top: -9999px;
}

.wfs-section {
  background: var(--w-bg-box);
  overflow: hidden;
  min-height: 60vh;
  padding: 30px;
  box-sizing: border-box;
}

.nav-evo-demo-list {
  display: flex;
  padding: 10px 20px;
  margin: 0 0 20px;
  list-style: none;
  text-align: center;
}
.nav-evo-demo-list li {
  margin: 0 10px;
}
.nav-evo-demo-list li a {
  color: var(--w-title-color);
  transition: color var(--w-th) ease-in-out;
}
.nav-evo-demo-list li a.active, .nav-evo-demo-list li a:hover {
  color: var(--w-pink);
}

.start-fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: var(--w-bg-box);
  border-bottom: 1px solid var(--w-border-color);
  box-sizing: border-box;
  z-index: 10;
}
.start-fixed .nav-evo-demo-list {
  margin: 0;
}

.open-sidebar-list {
  display: none;
}

@media (max-width: 1199px) {
  #header {
    margin-top: 52px;
  }
  .open-sidebar-list {
    width: 30px;
    height: 30px;
    margin: 10px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .open-sidebar-list path {
    stroke: var(--w-border-color);
    transition: stroke var(--w-th) ease-in-out;
  }
  .open-sidebar-list:hover path {
    stroke: var(--w-pink);
  }
  .wfs-nav-menu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background: var(--w-bg-box);
    border-bottom: 1px solid var(--w-border-color);
    border-top: 1px solid var(--w-border-color);
    display: flex;
    justify-content: flex-end;
  }
  .nav-evo-demo-list {
    position: fixed;
    height: 100%;
    padding-top: 100px;
    left: 100%;
    top: 0;
    padding: 60px 0 0;
    width: 100%;
    box-sizing: border-box;
    transition: left var(--w-th) ease-in-out;
    display: block;
    background: var(--w-bg-box);
  }
  .nav-evo-demo-list li {
    margin: 0 0 10px;
  }
  .nav-evo-demo-list a {
    display: block;
    text-align: left;
    padding: 10px 20px;
  }
  .open-mobile-menu .nav-evo-demo-list {
    left: 0;
  }
}